<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  setup() {
    const state = reactive({
      currentPage4:1,
      quantity_delivered:"",
      keyword: "",
      manufacturerId: "",
      manufacturerlist: "",
      product_classificationId: "",
