      dialogVisible: false,
      tableData: [
        {
          product_classification: "",
          quantity_delivered: "",
          deliverable_quantity: "",
          product_code: "",
          product_name: "",
          product_abbreviation: "",
          specification_model: "",
          unit: "",
          quantity: "",
          remarks: "",
          order_notes: "",
          unit_price: "",
          quantity_shipped: "",
          quantity_returned: "",
          amount_received: "",
          uncollected_amount: "",
          invoiced_amount: "",
          uninvoiced_amount: "",
          manufacturer: "",
          refunded_amount: "",
        },
      ],
    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    const handleSelectionChange = (val) => {
      proxy.multipleSelection = val;
    };
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    const search = () => {};
    const resetForm = () => {};
    const addcommodity = () => {};

    return {
      setshow,
      setedit,
      ...toRefs(state),
      handleSelectionChange,
      handleSizeChange,
      handleCurrentChange,
      search,
      resetForm,
      addcommodity,
    };
  },
};
</script>
